import { useState } from "react";

function Counter() {
  const [count, setCount] = useState(0);
  return (
    <div>
      <h1 data-testid="header-counter">Counter {count}</h1>
      <button onClick={() => setCount(count + 1)} data-testid="add-btn">
        ADD
      </button>
      <button onClick={() => setCount(count - 1)} data-testid="reduce-btn">
        REDUCE
      </button>
    </div>
  );
}

export default Counter;
